import { numberToLocale } from "./localize.js";

// Source: https://www.unicode.org/cldr/charts/32/summary/hi.html

const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "   ", // CLDR #1310
    other: "{{count}}   ",
  },

  xSeconds: {
    one: " ",
    other: "{{count}} ",
  },

  halfAMinute: " ",

  lessThanXMinutes: {
    one: "   ",
    other: "{{count}}   ",
  },

  xMinutes: {
    one: " ", // CLDR #1307
    other: "{{count}} ",
  },

  aboutXHours: {
    one: "  ",
    other: " {{count}} ",
  },

  xHours: {
    one: " ", // CLDR #1304
    other: "{{count}} ", // CLDR #4467
  },

  xDays: {
    one: " ", // CLDR #1286
    other: "{{count}} ",
  },

  aboutXWeeks: {
    one: "  ",
    other: " {{count}} ",
  },

  xWeeks: {
    one: " ",
    other: "{{count}} ",
  },

  aboutXMonths: {
    one: "  ",
    other: " {{count}} ",
  },

  xMonths: {
    one: " ",
    other: "{{count}} ",
  },

  aboutXYears: {
    one: "  ",
    other: " {{count}} ", // CLDR #4823
  },

  xYears: {
    one: " ",
    other: "{{count}} ",
  },

  overXYears: {
    one: "   ",
    other: "{{count}}   ",
  },

  almostXYears: {
    one: "  ",
    other: " {{count}} ",
  },
};

export const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace("{{count}}", numberToLocale(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return result + " ";
    } else {
      return result + " ";
    }
  }

  return result;
};
